<template>
	<div>
		<div>
			<a-button type="primary" icon="plus-circle" @click="showModal">
				添加
			</a-button>
			<UserAddPage :visible="visible" :ageList="ageList" :occupationList="occupationList" 
			@updateDataSource="updateDataSource" @updatePopWinState="updatePopWinState"></UserAddPage>
			<div style="display: flex; justify-content: flex-start;">
				<div>
					搜关键字：
					<a-input style="width:200px; margin: 20px 0 ;" v-model="params.key"
					 placeholder="用户名/邮编"/>
				</div>
				<div style="margin: 20px 0 0 25px">
					性别：
					<a-select v-model="params.gender" style="width: 150px;" placeholder="选择性别" :allowClear="true">
						<a-select-option value="M">男</a-select-option>
						<a-select-option value="F">女</a-select-option>
					</a-select>
				</div>
				<a-button type="primary" icon="search" style="margin: 20px 0 0 50px" @click="search">
					搜索
				</a-button>
			</div>

			<div v-show="!collapsed" style="display: flex; justify-content: flex-start;">
				<div>
					职业类型：
					<a-select v-model="params.occupation" style="width: 200px;" placeholder="选择职业" :allowClear="true">
						<a-select-option :value=key v-for="(value, key) in occupationList">
							{{value}}
						</a-select-option>
					</a-select>
				</div>
				<div style="margin-left: 25px;">
					年龄：
					<a-select v-model="params.age" style="width: 150px;" placeholder="选择年龄段" :allowClear="true">
						<a-select-option :value=key v-for="(value, key) in ageList">
							{{value}}
						</a-select-option>
					</a-select>
				</div>
			</div>
			<a-divider style="color: #999999; margin-top: -2px;">
				<a-icon @click="collapsed=!collapsed" :type="collapsed?'down-circle':'up-circle'" />
				{{collapsed?"展开":"收起"}}更多选项
			</a-divider>
		</div>

		<a-table :columns="columns" :data-source="dataSource" :loading="dataloading" :pagination=" {pageSize:pageSize}" :scroll="{ y: 280 }">
			<b slot="name" slot-scope="text">{{ text }}</b>
			<span slot="gender" slot-scope="text">{{ text=="M"?"男":"女" }}</span>
			<span slot="age" slot-scope="text" v-text="ageList[text]"></span>
			<span slot="occupation" slot-scope="text" v-text="occupationList[text]"></span>
			<span slot="action" slot-scope="text, record">
				<div style="display: flex; justify-content: center;">
					<a-popconfirm title="是否确定删除？" ok-text="是" cancel-text="否" @confirm="confirmDelete(record.uid)"
					 @cancel="cancel">
						<a>删除</a>
					</a-popconfirm>
					<a-divider type="vertical" />
					<router-view :record="record" :ageList="ageList" :occupationList="occupationList"></router-view>
					<a-divider type="vertical" />
					<a-popconfirm title="是否确定重置密码？" ok-text="是" cancel-text="否" @confirm="confirmReset(record.uid)"
					 @cancel="cancel">
						<a>重置密码</a>
					</a-popconfirm>
				</div>
			</span>
		</a-table>
	</div>

</template>

<script>
	import UserAddPage from './UserAddPage.vue'
	import {
		mapMutations,
		mapState
	} from "vuex";
	const columns = [
		{
			title: '用户名',
			dataIndex: 'name',
			scopedSlots: {
				customRender: 'name'
			},
		},
		{
			title: '性别',
			dataIndex: 'gender',
			scopedSlots: {
				customRender: 'gender'
			},
		},

		{
			title: '年龄',
			dataIndex: 'age',
			scopedSlots: {
				customRender: 'age'
			},
		},
		{
			title: '职业',
			dataIndex: 'occupation',
			scopedSlots: {
				customRender: 'occupation'
			},
		},
		{
			title: '邮编',
			dataIndex: 'zipCode',
		},
		{
			title: '操作',
			dataIndex: 'action',
			scopedSlots: {
				customRender: 'action'
			},
		},
	];
	columns.forEach((r) => {
		r.align = 'center'
	});

	export default {
		components: {
			UserAddPage
		},
		data() {
			return {	
				params : {
					key : null,
					age : null,
					occupation: null,
					gender : null,
				},
				dataSource: null,
				page: 1,
				pageSize: 10,
				dataloading: true,
				visible: false,
				columns,
				collapsed: true,
				ageList: {1:"Under 18",18:"18-24",25:"25-34",35:"35-44",45:"45-49",50:"50-55",56:"56+"},
				occupationList: ['other', 'academic/educator', 'artist', 'clerical/admin', 'college/grad student',
					'customer service', 'doctor/health care', 'executive/managerial', 'farmer', 'homemaker', 'K-12 student',
					'lawyer', 'programmer', 'retired', 'sales/marketing', 'scientist', 'self-employed',
					'technician/engineer', 'tradesman/craftsman', 'unemployed', 'writer'
				],
			};
		},
		computed: {
			...mapState(['requestPath', 'userInfo'])
		},
		
		async created() {
			this.getAllAge();
			this.getAllOccupation();
			this.getAllUser();
		},
		methods: {
			async getAllAge(){
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allAge');
					this.ageList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllOccupation(){
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allOccupation');
					this.occupationList = data.data;
				} else {
					console.log("userInfo not found");
				}
			},
			async getAllUser(){
				this.dataloading = true;
				if (this.$store.state.userInfo) {
					let {
						data
					} = await this.$get(this.$store.state.requestPath + 'allUser');
					this.dataSource = data.data;
					this.dataloading = false;
				} else {
					console.log("userInfo not found");
				}
			},
			async search(){
				this.dataloading = true;
				let {
					data
				} = await this.axios.post(this.requestPath + 'searchUser', this.params);
				this.dataSource = data.data;
				this.dataloading = false;
			},
			async confirmDelete(e) {
				console.log('del_uid:' + e);	
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'delUser/' + e);
				if (data.code == 0){
					this.$message.success('删除成功');
					const dataSource = [...this.dataSource];
					this.dataSource = dataSource.filter(item => item.uid !== e);
				}else{
					this.$message.error('删除失败');
				}	
			},
			async confirmReset(e) {
				console.log('reset_uid:' + e);	
				let {
					data
				} = await this.$get(this.$store.state.requestPath + 'resetUserPwd/' + e);
				if (data.code == 0){
					this.$message.success('重置密码成功');
				}else{
					this.$message.error('重置密码失败');
				}	
			},
			
			cancel(e) {
				console.log(e);
			},
			showModal() {
				this.visible = true;
			},
			updatePopWinState(state){
				this.visible = state;
			},
			async updateDataSource(){
				this.getAllUser();
			},
		},
	};
</script>

<style lang="scss" scoped>
	.selectStyle{
		width: 50px;
	}
</style>
