<template>
	<a-modal v-model="visible" title="编辑新用户" on-ok="handleOk" :afterClose="handleCancel" width="800px" :dialog-style="{ top: '100px' }">
		<template slot="footer">
			<a-button key="back" @click="handleCancel">
				取消
			</a-button>
			<a-button key="submit" type="primary" :loading="buttonloading" @click="handleOk">
				提交
			</a-button>
		</template>
		<div>
			<a-form :form="form" :label-col="labelCol" :wrapper-col="wrapperCol" hide-required-mark>
				<div style="margin-top: -10px; display: flex; justify-content: space-around;">
					<div>
						<a-form-item label="用户名">
							<a-input style="width: 300px;" v-decorator="['name', {rules: [
								{ required: true, message: '用户名不能为空' }],
								},
							]"
							 placeholder="请输入用户名"/>
						</a-form-item>
						<a-form-item label="性别">
							<a-select v-decorator="['gender']" style="width: 300px;" placeholder="请选择性别">
								<a-select-option value="M">男</a-select-option>
								<a-select-option value="F">女</a-select-option>
							</a-select>
						</a-form-item>
						<a-form-item label="年龄">
							<a-select v-decorator="['age']" placeholder="请选择年龄段" style="width: 300px;">
								<a-select-option :value=key v-for="(value, key) in ageList">
									{{value}}
								</a-select-option>
							</a-select>
						</a-form-item>
						<a-form-item label="职业">
							<a-select v-decorator="['occupation']" placeholder="请选择职业" style="width: 300px;">
								<a-select-option :value=key v-for="(value, key) in occupationList">
									{{value}}
								</a-select-option>
							</a-select>
						</a-form-item>
						<a-form-item label="邮政编码">
							<a-input style="width: 300px;" v-decorator="['zipCode', {rules: [
								{ required: true, message: '邮政编码不能为空' }],
								},
							]"
							 placeholder="请输入邮编" />
						</a-form-item>
					</div>
					<a-form-item>
						<a-upload name="file" list-type="picture-card" class="poster-uploader" :show-upload-list="false" :action="this.$store.state.requestPath + 'uploadHeadPic'"
						 :before-upload="beforeUpload" @change="handleChange">
							<img v-if="imageUrl" :src="imageUrl" style="width: 200px; height: 300px;" />
							<div v-else>
								<a-icon :type="picloading ? 'loading' : 'plus'" />
								<div class="ant-upload-text">
									上传
								</div>
							</div>
						</a-upload>
					</a-form-item>
				</div>
			</a-form>
		</div>
	</a-modal>
</template>
<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'

	function getBase64(img, callback) {
		const reader = new FileReader();
		reader.addEventListener('load', () => callback(reader.result));
		reader.readAsDataURL(img);
	};
	export default {
		computed: {
			...mapState(['userInfo', 'requestPath'])
		},
		props: ['visible','ageList','occupationList'],
		data() {
			return {
				form: this.$form.createForm(this),
				labelCol: {
					span: 4
				},
				wrapperCol: {
					span: 14
				},
				imageUrl: '',
				newRow: {
					"mid": null,
					"name": null,
					"genre": null,
					"releaseTime": null,
					"directors": null,
				},
				user: {
					"name": "undefine",
					"age": "undefine",
					"gender": "undefine",
					"occupation": "undefine",
					"zipCode": "undefine",
					"headPic": "default.jpg",
				},
				picloading: false,
				buttonloading: false,
			};
		},
		methods: {
			async handleOk(e) {	
				this.form.validateFields(async (err, values) => {
					if (!err) {
						this.user.name = values.name;
						this.user.age = values.age;
						this.user.gender = values.gender;
						this.user.occupation = values.occupation;
						this.user.zipCode = values.zipCode;
						console.log(this.user);
						let {
							data
						} = await this.axios.post(this.requestPath + 'addUser', this.user);
						if (data.code == 0) {						
							this.$message.success('添加成功');
							this.$emit('updateDataSource');
						}else{
							this.$message.error('添加失败');
						}
					}
				});
				this.buttonloading = true;
				setTimeout(() => {
					this.$emit('updatePopWinState', false);
					this.buttonloading = false;
				}, 2000);
			},
			handleCancel(e) {
				this.imageUrl = null;
				this.$emit('updatePopWinState', false);
			},
			handleChange(info) {
				if (info.file.status === 'uploading') {
					this.picloading = true;
					return;
				}
				if (info.file.status === 'done') {
					// 从前端获取上传的 img-url
					getBase64(info.file.originFileObj, imageUrl => {
						this.imageUrl = imageUrl;
						this.picloading = false;
					});
					this.user.headPic = info.file.response.data.src;
				}
			},
			beforeUpload(file) {
				const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
				if (!isJpgOrPng) {
					this.$message.error('You can only upload JPG file!');
				}
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isLt2M) {
					this.$message.error('Image must smaller than 2MB!');
				}
				return isJpgOrPng && isLt2M;
			},
		},
	}
</script>

<style lang='scss'>
	.poster-uploader>.ant-upload {
		width: 200px;
		height: 240px;
	}
	
	.ant-upload-select-picture-card i {
		font-size: 16;
		color: #999;
	}
	
	.ant-upload-select-picture-card .ant-upload-text {
		margin-top: 8px;
		color: #666;
	}
</style>
